<template>
  <div class="box">
    <div class="top">顶部固定区域顶部固定区域顶部固定区域</div>
    <div class="content">
      <img src="./assets/cloud-left.png" class="cloud-1" />
      <img src="./assets/cloud-right.png" class="cloud-2" />

      <img src="./assets/cloud-left.png" class="cloud-3" />
      <img src="./assets/cloud-right.png" class="cloud-4" />
      <img src="./assets/bg.jpg" class="bg" alt="" />
    </div>
  </div>
</template>

<script>
import gsap from "gsap";
export default {
  name: "App",
  mounted() {
    const box = document.querySelector(".content");
    const bgImg = document.querySelector(".bg");
    const initValue =
      box.getBoundingClientRect().height - bgImg.getBoundingClientRect().height;
    console.log(initValue);
    var t1 = gsap.timeline();
    var t2 = gsap.timeline();
    t1.to(".content", { scrollTop: initValue });
    t1.to(".content", { scrollTop: 200, duration: 2 });

    t1.to(".cloud-1", {
      left: "-100%",
      onStart: function () {
        t2.to(".cloud-2", { left: "100%" });
      },
    });

    t1.to(".content", { scrollTop: 0, duration: 1 });
    t1.to(".cloud-3", {
      left: "-100%",
      onStart() {
        t2.to(".cloud-4", { left: "100%" });
      },
    });
  },
};
</script>

